import about_me_card from "@/components/about_me/about_me_card/about_me_card.vue";

export default {
	components: {
		about_me_card,
	},
	props: {
		title: String,
		description: String,
		project_type: String,
		year: String,
		page: String,
		video: String,
		video_overlay_image: String,
		skills_images: Array,
	},

	data() {
		return {
			video_overlay: null,
		}
	},

	methods: {
		mouseover(e) {
			e.target.play();
			this.video_overlay.style.opacity = '0';
		},

		mouseleave(e) {
			e.target.pause();
			this.video_overlay.style.opacity = '1';
		}
	},

	mounted() {
		this.video_overlay = this.$el.querySelector('.video-overlay');
	}
}
